@import url('https://fonts.googleapis.com/css?family=Numans');


.login-container{
height: 100%;
align-content: center;
}

.card{
height: 370px;
margin-top: auto;
margin-bottom: auto;
width: 1500px;
background-color: rgba(0,0,0,0.5) !important;
}



.card-header h3{
color: white;
}


.input-group-prepend span{
width: 50px;
background-color: #FFC312;
color: black;
border:0 !important;
}

input:focus{
