.App {
    font-family: sans-serif;
    text-align: center;
  }

  .column {
    float: left;
    width: 15%;
    padding: 1px;
  }
  .row {
      margin-bottom: 50px;
  }
  /* Clearfix (clear floats) */
  .row::after {
    content: "";
    clear: both;
    display: table;
  }
  
  .override {
    color:white;
    position: fixed;
    z-index: 1;
    transition: 0.5s;
  }

  /* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 500px) {
    .column {
      width: 100%;
    }
  }
